	export default {
		data() {
			return {
			  api:this.GLOBAL.baseURL,
				activeNames:[],
				msgData:[],
        txData:[],
        userData:''
			};
		},
		methods:{
      change(){
        console.log(this.msgData)
      },
			handleChange(){
				
			},
			toEditdata(){
				this.$router.push('/edit')
			},
      saveNotice(){
			  this.$http.post(this.api + '/home/User/config_add',{
			    token: localStorage.getItem('token'),
          mes: this.msgData ,
          remind: this.txData ,
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message: '保存成功！'
              })
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.saveNotice()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getInformation(){
			  this.$http.post(this.api + '/home/User/get_user',{
			    token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              this.userData = res.data.data[0]
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getInformation()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg )
            }
          })
      },
      getNotice(){
			  this.$http.post(this.api + '/home/User/config_get',{
			    token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              if(data.mes){
                var a = []
                for(let m = 0;m<data.mes.split(',').length;m++){
                  a.push(parseInt(data.mes.split(',')[m]))
                }
                this.msgData = a
              }
              if(data.remind){
                var a = []
                for(let m = 0;m<data.remind.split(',').length;m++){
                  a.push(parseInt(data.remind.split(',')[m]))
                }
                this.msgData = a
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getNotice()
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
		},
    mounted(){
		  this.getInformation()
      this.getNotice()
    }
	}



